
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import { getPortfolioList } from '@/api/portfolio'
import { imgBaseUrl } from '@/utils/config'

export default {
name: 'Portfolio',
components: { Flexbox, FlexboxItem, InlineLoading },
data () {
return {
showLoading: true,
listData: []
}
},
created () {
this.getPortfolioList()
},
methods: {
handleClick (uri) {
window.location.href = imgBaseUrl + uri
},
getPortfolioList () {
this.showLoading = true
getPortfolioList().then(res => {
this.showLoading = false
this.listData = res.data
}).catch(res => {
this.showLoading = false
})
}
}
}
